<template>
    <div>
        <div class="pcenter-header">
            <div class="header-title">个人中心</div>
        </div>
        <!--pcenter-user-->
        <div class="pcenter-user">
            <div class="head">
                <img :src="isLogin?head:require('../../assets/images/user/my/default-head.png')" alt="">
            </div>
            <div class="nickname">昵称:{{isLogin?nickname:""}}</div>
            <div class="points">我的积分:{{isLogin?points:0}}</div>
        </div>
        <!--pcenter-order-->
        <div class="pcenter-order">
            <div class="order-Lname">全部订单</div>
            <div class="order-Rname"   @click="$router.push('/user/order?status=all')">查看更多订单</div>
        </div>
        <!--pcenter-status-->
        <div class="pcenter-status">
            <div class="status-item">
                <div class="icon wait"  ></div>
                <div class="text" @click="$router.push('/user/order?status=0')">待支付</div>
            </div>
            <div class="status-item">
                <div class="icon take"  ></div>
                <div class="text" @click="$router.push('/user/order?status=1')">待收货</div>
            </div>
            <div class="status-item">
                <div class="icon comment"  ></div>
                <div class="text" @click="$router.push('/user/order/review?status=2')">待评价</div>
            </div>
        </div>
        <!--pcenter-list-->
        <div class="pcenter-list">
            <ul @click="$router.push('/user/profile')">
                <li >个人资料</li>
                <li></li>
            </ul>
            <ul @click="$router.push('/address?pcenter')">
                <li>收货地址</li>
                <li></li>
            </ul>
            <ul @click="$router.push('/user/bind_cellphone')">
                <li>绑定手机</li>
                <li></li>
            </ul>
            <ul @click="$router.push('/user/mod_password')">
                <li>修改密码</li>
                <li></li>
            </ul>
            <ul @click="$router.push('/user/fav')">
                <li>我的收藏</li>
                <li></li>
            </ul>
            <div  class="btn" @click="isLogin?onOutlogin():$router.push('/login')">{{isLogin?'安全退出':'登录/注册'}}</div>
        </div>
    </div>
</template>

<script>
    import {mapState,mapActions} from "vuex"
    import Vue from 'vue';
    import { Dialog } from 'vant';
    Vue.use(Dialog);
    export default {
        computed:{
            ...mapState({
                isLogin:(state)=>state.user.isLogin,
                head:(state)=>state.user.head,
                points:(state)=>state.user.points,
                nickname:(state)=>state.user.nickname

            })
        },
        mounted(){
            document.title = this.$route.meta.title
        },
        created(){

            //获取会员信息
            this.getUserInfo();
        },
        methods:{
            ...mapActions({
                //安全退出
                getOutlogin:"user/getOutlogin",
                //获取会员信息
                getUserInfo:"user/getUserInfo"
            }),
            //安全退出
            onOutlogin(){
                Dialog.confirm({
                    title: '',
                    message: '确定要退出吗?',
                })
                    .then(() => {
                        this.getOutlogin();
                    })
                    .catch(() => {
                        // on cancel
                    });

            }
        }
    }
</script>

